@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.max_btn {
  border-radius: 15px;
  padding: 4px 14px;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  white-space: nowrap;
  @include darkThemeOnly {
    background: #251f32;
    border: 1px solid rgba(255, 255, 255, 0.1);
    color: #fff;
  }
  @include lightThemeOnly {
    background: #decdff;
    border: 1px solid #722fff;
    color: #5a14e1;
  }
}
