@import '../../assets/css/mixins';
$color-button-primary: #6400ff;

.sun-action-btns {
  button {
    border-radius: 6px;
    border: none;
    &.single {
      width: 100%;
      background: $color-button-primary;
      border-radius: 6px;
      color: #fff;
      cursor: pointer;
      display: block;
      margin: 0 auto;
      font-size: 20px;
      height: 50px;
      border-radius: 6px;
      font-family: Poppins;
      text-align: center;
      &:hover {
        background: linear-gradient(90.44deg, #6577ff 0%, #6400ff 99.25%);
        box-shadow: 0px -4px 0px 0px rgba(9, 1, 23, 0.3019607843) inset;
      }

      &.single-disabled {
        background: $color-button-primary;
        opacity: 0.5;
        cursor: not-allowed;
        &:hover {
          box-shadow: none;
        }
      }
      &.gray {
        cursor: not-allowed;
        color: rgba(255, 255, 255, 0.3);
        background: #443564;
        &:hover {
          background: #443564;
        }
      }

      &.half {
        width: 49%;
        max-width: 260px;
        margin-left: 2%;

        &:first-child {
          margin-left: 0;
        }
      }

      &.trisection {
        width: 32%;
        max-width: 160px;
        margin-left: 25px;

        &:first-child {
          margin-left: 0;
        }
      }

      &.blue {
        background: $color-button-primary;
        color: #fff;
        &:hover {
          background: linear-gradient(90.44deg, #6577ff 0%, #6400ff 99.25%);
          box-shadow: 0px -4px 0px 0px rgba(9, 1, 23, 0.3019607843) inset;
        }
      }

      &.orange {
        background: #ff8c00;
        color: #fff;
        &:hover {
          background: linear-gradient(90.44deg, #ff4d3d 0%, #ffa200 99.25%);
          box-shadow: 0px -4px 0px 0px rgba(9, 1, 23, 0.3019607843) inset;
        }
      }
    }
  }
  button {
    &.schedule-btn {
      margin-right: 33px;
      width: 100px;
      height: 40px;
      line-height: 40px;
      border-radius: 6px;
      background: rgba($color: $color-button-primary, $alpha: 0.2);
      color: $color-button-primary;
      font-size: 16px;
      font-family: Poppins;

      &:last-child {
        margin-right: 0;
      }

      &:hover {
        background-color: #8c5bff;
      }
    }

    &.schedule-btn-active {
      background: $color-button-primary;
      height: 40px;
      line-height: 40px;
      color: #fff;
    }

    &.half {
      width: 49%;
      max-width: 260px;
      margin-left: 2%;

      &:first-child {
        margin-left: 0;
      }
    }

    &.trisection {
      width: 32%;
      max-width: 160px;
      margin-left: 25px;

      &:first-child {
        margin-left: 0;
      }
    }

    &.gray {
      cursor: not-allowed;
      color: rgba(255, 255, 255, 0.3);
      background: #443564;
      &:hover {
        background: #443564;
        box-shadow: none;
      }
    }

    &.blue {
      background: $color-button-primary;
      color: #fff;
      position: relative;

      &:hover {
        background: linear-gradient(90.44deg, #6577ff 0%, #6400ff 99.25%);
        box-shadow: 0px -4px 0px 0px rgba(9, 1, 23, 0.3019607843) inset;
      }

      .ques {
        position: absolute;
        top: 26px;
        right: 26px;
      }
    }

    &.purple {
      background: rgba($color: $color-button-primary, $alpha: 0.2);
      color: $color-button-primary;
    }

    &.green {
      background: rgba($color: #16c378, $alpha: 0.2);
      color: #16c378;
    }

    &.ant-pagination-item-link {
      height: 32px;
      line-height: 32px;
    }

    &.orange {
      background: #ff8c00;
      color: #fff;
      &:hover {
        background: linear-gradient(90.44deg, #ff4d3d 0%, #ffa200 99.25%);
        box-shadow: 0px -4px 0px 0px rgba(9, 1, 23, 0.3019607843) inset;
      }
    }

    &.red {
      background: #e82a50;
      color: #fff;
      &:hover {
        background: linear-gradient(90.44deg, #ff0091 0%, #ff4800 99.25%);
        box-shadow: 0px -4px 0px 0px rgba(9, 1, 23, 0.3019607843) inset;
      }
    }
  }
}
@include mobile-only {
  .sun-action-btns {
    button.single {
      height: 40px;
      font-size: 16px;
    }
  }
}
