@import './var.scss';
@import './theme.scss';
@import './mixins';

.currency_label {
  .icon {
    margin-right: 0.6em;
  }
}

.token-logo {
  display: inline-block;
  height: 20px;
  width: 20px;
  vertical-align: middle;
  margin-right: 6px;
  margin-left: 0;
  border-radius: 50%;
  border: none;
}
.add-liq,
.exchange-box {
  .token-logo {
    height: 20px;
    width: auto;
    border: none;
    border-radius: 0;
  }
}

.purple-text {
  color: $color-primary;
  display: block;
}

.cur-label {
  margin-top: 10px;
  margin-bottom: 3px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: $color-text-default;

  .icon {
    display: inline-flex;
    align-items: center;
  }
}

.cur-reserve-container {
  background: #fff;
  border-radius: 15px;
  margin: 30px auto;
  padding: 30px 24px;
  max-width: min(728px, 90vw);
  box-sizing: border-box;

  @include mobile-only {
    width: calc(100% - 30px);
    margin: 15px auto;
    padding: 20px 15px;
  }
}

.cur-reserve,
.my-share {
  display: inline-block;
  text-align: left;
  vertical-align: top;
  font-size: 12px;
  line-height: 24px;

  @include desktop-only {
    width: 50%;
  }

  @include mobile-only {
    width: 100%;
  }

  .title-text {
    font-size: 18px;
    line-height: 16px;
    font-weight: bold;
    margin-bottom: 30px;

    @include mobile-only {
      margin-bottom: 20px;
      text-align: center;
    }
  }

  ul li {
    line-height: 22px;
  }
}

.cur-reserve {
  @include desktop-only {
    flex-grow: 1;
  }

  &:not(:last-child) {
    @include mobile-only {
      margin-bottom: 30px;
    }
  }
}

.token-max {
  display: inline-block;
  margin-left: 15px;
  cursor: pointer;
  font-size: 12px;
  span {
    font-size: 12px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: $color-text-default;
  }
}

// .token-max:hover {
//   text-decoration: underline;
// }

.error-tip {
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: $color-warning;
}

.error-content {
  min-height: 40px;
  // padding: 0 10px;
}

.add-liq {
  fieldset {
    // margin-top: 10px !important;
  }

  .percentage {
    // margin-top: 20px !important;
  }

  .currencies ul li {
    margin-top: 30px;
  }
  .currencies ul li:first-child {
    margin-top: 0;
  }
}

.action-btn {
  margin-top: 30px;
  margin-bottom: 20px;

  button {
    line-height: 18px;
  }
}

.withdraw-liq {
  legend {
    font-size: 14px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: $color-text-default;
  }

  .withdraw-one {
    overflow: hidden;

    .token-logo {
      transform: translateY(-1px);
    }

    > div {
      float: left;
      margin-top: 10px;
      margin-right: 10px;
    }
  }
}

.incl-price {
  padding: 12px 0 0 0;
  text-align: center;
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  span {
    margin-left: 6px;
  }
}

.gray-color {
  color: $color-text-default;
}

.blue-color {
  color: #28caba;
}
.high-slippage {
  color: $color-error !important;
  font-size: 12px;
  border-radius: 5px;
}

.swap-detail {
  // background: #4180db;
  // color: #fff;
  padding: 10px;
  color: #000;
  background: #ececec;
  border-radius: 5px;

  .slippage-tip {
    color: #999;
  }
}

.common-err-bg {
  background: #ec5a27;
  color: #fff;
}

.pad10 {
  padding: 10px;
}

.slippage-modal {
  .common-err-bg {
    padding: 10px 10px;
    text-align: left;
    color: #fff;
  }

  .ant-modal-title {
    text-align: center;
  }

  .ant-modal-body {
    padding-top: 0;
  }

  .ant-checkbox-wrapper {
    margin: 10px 0;

    > span:last-child {
      color: #e60c0e;
    }
  }

  .btns {
    justify-content: center;

    > button:first-child {
      margin-right: 10px;
    }
  }
}

.max-slippage {
  margin-left: 0px;
  margin-top: 5px;
}

.pc-adjust {
  margin-top: -2px;
  margin-right: 10px;
}

.switch3sun {
  margin: 10px 0;

  .ant-checkbox-wrapper {
    span:last-child {
      padding-left: 13px;
    }
  }
}

@media screen and (max-width: 500px) {
  .action-btn {
    margin-top: 10px;
    margin-bottom: 16px;
  }

  .add-liq,
  .withdraw-liq {
    .anticon {
      margin-top: 2px;
    }
  }

  .mobile-block {
    div:nth-child(1) {
      margin-top: 10px;
    }

    .ad-radios .ad-radio {
      margin-top: -5px;
    }
  }

  #root .ad-radio .ant-input {
    height: 26px;
  }
}
