@import '../../assets/css/theme';
@import '../../assets/css/mixins';

.currency {
  &_card {
    background: $color-card-bg-active;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px;
    @include mobile-only {
      padding: 10px;
    }
  }
  &_header {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    @include responseFont(20px, 16px);
    font-weight: 600;
    color: $color-text-default;
    height: 40px;
    align-items: center;
    margin-bottom: 10px;
    &_icon {
      width: 40px;
      height: 40px;
      margin-left: -10px;
    }
  }
  &_pool_icon {
    display: block;
    height: 40px;
  }
  &_total_balance {
    font-size: 14px;
    font-weight: 600;
    color: $color-text-default-7;
    line-height: 20px;
    margin-bottom: 8px;
    @include responseFont(14px, 12px);
  }
  &_total_value {
    font-size: 30px;
    font-weight: bold;
    color: $color-text-default;
    line-height: 36px;
    margin-bottom: 20px;
    font-family: Helvetica-Bold, Helvetica;
    @include responseFont(30px, 20px);
  }
  &_block {
    background: $color-info-bg;
    border-radius: 10px;
    padding: 0 16px;
    margin-bottom: 20px;
    @include lightThemeOnly {
      border: 1px solid $color-border-secondary;
    }
    @include mobile-only {
      padding: 0 12px;
    }

    &_item {
      padding: 19px 0;
      display: flex;
      align-items: center;
      border-bottom: 1px solid $color-border-secondary;
      &:last-child {
        border: none;
      }
      @include mobile-only {
        padding: 12px 0;
      }
    }
  }
  &_block_normal {
    background: $color-info-bg;
    border-radius: 10px;
    padding: 16px;
    margin-bottom: 20px;
    @include mobile-only {
      padding: 12px;
    }
    @include lightThemeOnly {
      border: 1px solid $color-border-secondary;
    }
    &_item {
      padding-bottom: 20px;
      display: flex;
      align-items: center;
      &:last-child {
        padding-bottom: 0;
      }
    }
  }
  &_label {
    @include responseFont(16px, 14px);
    font-weight: 600;
    color: $color-text-default-5;
    line-height: 22px;
  }
  &_label_light {
    font-weight: 600;
    color: $color-text-default;
    line-height: 22px;
    @include responseFont(16px, 14px);
  }
  &_value {
    font-weight: 600;
    color: $color-text-default;
    line-height: 28px;
    margin-left: auto;
    @include responseFont(20px, 16px);
  }
  &_value_mini {
    font-size: 16px;
    font-weight: 600;
    color: $color-text-default;
    line-height: 22px;
    margin-left: auto;
  }
  &_tag {
    display: inline-block;
    font-size: 12px;
    font-weight: 400;
    color: $color-text-default-7;
    background-color: $color-text-default-1;
    line-height: 17px;
    padding: 2px 6px;
    margin-left: 8px;
  }
}

.currency_link a {
  &:visited,
  &:link {
    color: $color-text-default;
  }
}
@media screen and (max-width: 768px) {
  .currency_link a {
    display: block;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
